"use client";

import Link from "next/link";
import { useSupabaseSession } from "./SupabaseSessionProvider";
import { Button } from "./ui/button";

export function SiteHeader() {
  const { session, supabase } = useSupabaseSession();
  const email = session?.user?.email as string | undefined;

  async function handleLogout() {
    await supabase.auth.signOut();
    // Simple hard reload back to practice page
    window.location.href = "/";
  }

  return (
    <header className="w-full border-b border-slate-200 bg-white">
      <div className="mx-auto flex max-w-3xl items-center justify-between px-6 py-3">
        {/* Left side: logo + nav */}
        <div className="flex items-center gap-6">
          <Link
            href="/"
            className="text-sm font-semibold tracking-tight text-slate-900"
          >
            IELTS Writing Practice
          </Link>

          <nav className="hidden items-center gap-4 text-xs text-slate-600 sm:flex">
            <Link href="/" className="hover:text-slate-900">
              Practice
            </Link>
            <Link href="/attempts" className="hover:text-slate-900">
              My attempts
            </Link>
            <Link href="/progress" className="hover:text-slate-900">
              Progress
            </Link>
            <Link href="/profile" className="hover:text-slate-900">
              Profile
            </Link>
          </nav>
        </div>

        {/* Right side: signed in + auth button */}
        <div className="flex items-center gap-3">
          {email && (
            <p className="hidden text-xs text-slate-500 sm:inline">
              Signed in as{" "}
              <span className="font-mono text-slate-800">{email}</span>
            </p>
          )}

          {session ? (
            <Button
              size="sm"
              variant="outline"
              className="text-xs"
              onClick={handleLogout}
            >
              Logout
            </Button>
          ) : (
            <Button size="sm" className="text-xs" asChild>
              <Link href="/auth/login">Login</Link>
            </Button>
          )}
        </div>
      </div>
    </header>
  );
}
